@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onTablet {
  @media (min-width: 640px) {
    @content;
  }
}

@mixin smallDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: 1280px) {
    @content;
  }
}

@mixin theDesign {
  @media (min-width: 1600px) {
    @content;
  }
}

@mixin largeDesktop {
  @media (min-width: 2560px) {
    @content;
  }
}
